import React, { Component } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
  Modal, ModalBody, ModalFooter,
} from 'reactstrap';
import { addUser } from '../../actions/authActions';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {withRouter} from 'react-router-dom';

class index extends Component {
  constructor(){
    super();
    this.state ={
      username:'',
      password:'',
      confirmPassword:'',
      errors:{},
      success: false,
    }
    this.toggleSuccess = this.toggleSuccess.bind(this);

  }
  onChange =(e) =>{
    this.setState({[e.target.name]: e.target.value});
  }

  toggleSuccess() {
    this.setState({
      success: !this.state.success,
    });
  }

  toggleSuccessOk =() => {
    this.setState({
      success: false,
      username:'',
      password:'',
      confirmPassword:'',
      errors:{},
    });
  }


  onSubmit =(e) =>{
    e.preventDefault();
    const userData ={
      username:this.state.username,
      password:this.state.password,
      confirmPassword: this.state.confirmPassword
    }
    this.props.addUser(userData,this.toggleSuccess);


  }
  reset =() =>{
    this.setState({
      username:'',
      password:'',
      confirmPassword:'',
      errors:{},
      success: false,
    })
  }
  componentWillReceiveProps(nextProps){
    if(nextProps.errors){
      console.log("errors"+nextProps.errors);
      this.setState({errors: nextProps.errors});
    }
  }

  render() {
    const { errors } = this.state ;
    return (
      <div>
        <Row id='printarea'>
        <Col xs="12" sm="6">
            <Card>
              <CardHeader>
                <strong>User</strong>
                <small> Form</small>
              </CardHeader>
              <CardBody>
                <FormGroup>
                  <Label htmlFor="company">User Name</Label>
                  <Input type="text" id="username" placeholder="Enter user name" name="username" value={this.state.username} onChange={this.onChange}/>
                  {/* {errors.clientName && (<FormFeedback valid className="help-block">Input provided</FormFeedback>)} */}
                  {errors.username && (<lable style={{color: "red"}}>{errors.username}</lable>)}
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="vat">Password</Label>
                  <Input type="password" id="vat" placeholder="Enter password" name="password" value={this.state.password} onChange={this.onChange}/>
                  {errors.password && (<lable style={{color: "red"}}>{errors.password}</lable>)}

                </FormGroup>
                <FormGroup>
                  <Label htmlFor="street">Comfirm Password</Label>
                  <Input type="password" id="confirmPassword" placeholder="Enter comfirm password" name="confirmPassword" value={this.state.confirmPassword} onChange={this.onChange}/>
                  {errors.confirmPassword && (<lable style={{color: "red"}}>{errors.confirmPassword}</lable>)}
                </FormGroup>

              </CardBody>
              <CardFooter>
                <Button type="submit" size="sm" color="primary" onClick={this.onSubmit}><i className="fa fa-dot-circle-o"></i> Submit</Button>
                <Button type="reset" size="sm" color="danger"  onClick={this.reset}><i className="fa fa-ban"></i> Reset</Button>
              </CardFooter>
            </Card>
          </Col>
          </Row>
          <Modal isOpen={this.state.success} toggle={this.toggleSuccess}
                       className={'modal-success ' + this.props.className}>
                  {/* <ModalHeader toggle={this.toggleSuccess}>Modal title</ModalHeader> */}
                  <ModalBody>
                  “User added successfully”
                  </ModalBody>
                  <ModalFooter>
                    <Button color="success" onClick={this.toggleSuccessOk}>Ok</Button>{' '}
                    {/* <Button color="secondary" onClick={this.toggleSuccess}>Cancel</Button> */}
                  </ModalFooter>
                </Modal>
      </div>
    );
  }
}

//export default index;

const mapStateToProps =(state) =>({
  auth:state.auth,
  errors:state.errors
})

export default connect (mapStateToProps,{addUser})(withRouter(index));
